
import saleRepotService from '../service/saleRepotService'
import optionService from '../service/optionService'
import Alert from 'vux/src/components/alert/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import querystring from 'vux/src/tools/querystring/index.js'
import numberComma from 'vux/src/tools/number/comma.js'

export default {
components:{
Cell,
Group,
XInput,
Confirm,
XButton,
Selector,
PopupPicker,
Alert
},
data () {
return {
alertEnd:false,
list: [],
arr:[
{
value:[],
qty:''
}
],
Aclass:'',
Bclass:'',
showNumber:false,
showNewDiv:false,
mescroll: null,
totalInfo:'',
areaList:[],
areaValue:[],
bankList:[],
bankValue:[],
teamLeaderList:[],
teamLeaderValue:[],
helpCaptain:'',
captainShow:false,
}
},
filters:{
numberComma
},
methods:{

getPickerValue(val){

},
onChange(val){
},
createNew(){
this.arr.push({
value: [],
qty:''
}); //
},
deleteNew(){
if(this.arr.length==1){
this.arr=[
{
value: [],
qty:''
}
]
}else{
this.arr.splice(this.arr.length-1,1); 
}
},

guid() {
function S4() {
return (((1+Math.random())*0x10000)|0).toString(16).substring(1);
}
return (S4()+S4()+"-"+S4()+"-"+S4()+"-"+S4()+"-"+S4()+S4()+S4());
},
listData(){
saleRepotService.saleRepotList().then(data=>{
for(let i = 0 ; i<data.tableContent.length ; i++){
this.list.push({
name:data.tableContent[i]['trans_detail_uncalc.transObjCode'],
value:data.tableContent[i]['trans_detail_uncalc.transObjCode']+'_'+i,
parent:'0',
},{
name:data.tableContent[i]['trans_detail_uncalc.price'],
value:data.tableContent[i]['trans_detail_uncalc.price'],
parent:data.tableContent[i]['trans_detail_uncalc.transObjCode']+'_'+i,
});
}
})
},
end(){
var that=this;

if(this.areaValue.length==0){
this.$vux.alert.show({
title: '',
content: '',
onShow () {

},
onHide () {

}
})
return;
}else if(this.bankValue.length==0){
this.$vux.alert.show({
title: '',
content: '',
onShow () {

},
onHide () {

}
})
return;
}else if(this.arr[0].value.length==0){
this.$vux.alert.show({
title: '',
content: '',
onShow () {

},
onHide () {

}
})
return;
}else if(this.helpCaptain==''){
this.$vux.alert.show({
title: '',
content: '',
onShow () {

},
onHide () {

}
})
return;
}
let dept =JSON.parse(localStorage.getItem('ROSE_OPTION')).dept;
localStorage.setItem('ROSE_OPTION',JSON.stringify({bank:this.bankValue[0],captain:this.helpCaptain,dept:dept,region:this.areaValue[0]}))


var jsonData = {
"listId": "4bda3e47-a088-4749-a988-ebb07cfb00e4",
"referenceId":this.guid(),
"baseinfoExt": {
"id":this.guid(),
"varchar1": JSON.parse(localStorage.getItem('ROSE_OPTION')).dept,
"varchar2": JSON.parse(localStorage.getItem('ROSE_OPTION')).captain,
"varchar3": JSON.parse(localStorage.getItem('ROSE_OPTION')).region,
"varchar4": JSON.parse(localStorage.getItem('ROSE_OPTION')).bank,
},
"transDetailUncalc": [],
"transCode": "XHXSDD"
};

for(let i =0 ;i<this.arr.length ; i++){
if(this.arr[i].qty==''){
this.$vux.alert.show({
title: '',
content: '',
onShow () {

},
onHide () {

}
})
return;
}else{
jsonData.transDetailUncalc.push({
"id":this.guid(),
"transObjCode": this.arr[i].value[0].split('_')[0],//
"containerCode": "",//
"qty": this.arr[i].qty,
"amount": this.arr[i].qty*this.arr[i].value[1],//
"fgCode": ""
})
}
}
if(this.Aclass==''){
this.$vux.alert.show({
title: '',
content: 'A',
onShow () {

},
onHide () {

}
})
return;
}else if(this.Bclass==''){
this.$vux.alert.show({
title: '',
content: 'B',
onShow () {

},
onHide () {

}
})
return;
}else{
jsonData.transDetailUncalc.push({
"id":this.guid(),
"transObjCode": "A",//
"containerCode": "A",//
"qty": '',
"amount": this.Aclass,//
"fgCode": ""
},{
"id":this.guid(),
"transObjCode": "B",//
"containerCode": "B",//
"qty": '',
"amount": this.Bclass,//
"fgCode": ""
})
}
let totalInfo={
'isMobile':true,
'conn':20000,
'list':'trans_form_data',
'transCode':'XHXSDD',
'jsonData':JSON.stringify(jsonData)
}
this.totalInfo=totalInfo;
localStorage.setItem('saleReportInfo',JSON.stringify({saleReportRemark:totalInfo,time:new Date().getTime()}))
localStorage.setItem('saleReport',JSON.stringify({saleReportArr:this.arr,Aclass:this.Aclass,Bclass:this.Bclass,time:new Date().getTime()}))
that.$router.push({path:'/count'});
},
Ainput(e){
this.Aclass=e.split(',').join('');
},
Binput(e){
this.Bclass=e.split(',').join('');
},
letMeTest(){
let path = this.$router.path;
},

getArea(){
optionService.getRegion().then(data=>{
for(let i = 0 ;i<data.length; i++){
this.areaList.push({
name:data[i].name,
value:data[i].name,
})
}
})
},

getBank(){
optionService.getBank().then(data=>{
for(let i = 0 ; i<data.tableContent.length ;i++){
data.tableContent[i].name=data.tableContent[i].bankName;
data.tableContent[i].value=data.tableContent[i].bankName;
}
this.bankList=data.tableContent;
})
},

teamLeader(){
optionService.getCaptain().then(data=>{
for(let i = 0 ; i<data.tableContent.length ;i++){
data.tableContent[i].name=data.tableContent[i].nickname;
data.tableContent[i].value=data.tableContent[i].nickname;
}
this.teamLeaderList=data.tableContent;
})
},

captainSelect(e){
if(this.captainShow==false||this.helpCaptain==''){
return;
}
optionService.getCaptain({value:e}).then(data=>{
this.teamLeaderList=data.tableContent;
})
},
captainFocus(){
this.captainShow=true;
},

getNickname(e){
this.helpCaptain=e;
this.captainShow=false;
this.teamLeaderList=[]
},
getPickerArea(e){

},
},
created(){

},
mounted(){
this.getArea();
this.getBank();

if(localStorage.getItem('saleReport')){
this.arr=JSON.parse(localStorage.getItem('saleReport')).saleReportArr;
this.Aclass=JSON.parse(localStorage.getItem('saleReport')).Aclass;
this.Bclass=JSON.parse(localStorage.getItem('saleReport')).Bclass;
this.helpCaptain=JSON.parse(localStorage.getItem('saleReport')).captain;
}
if(localStorage.getItem('ROSE_OPTION')){
this.areaValue=[JSON.parse(localStorage.getItem('ROSE_OPTION')).region];
this.bankValue=[JSON.parse(localStorage.getItem('ROSE_OPTION')).bank];

}
this.listData();
this.mescroll = new MeScroll("mescroll",{
up:{
isBounce:false,
use:false,
auto:false
},
down:{
use:false,
auto:false,
isLock:false
}

}
)



},
beforeRouteLeave(to,from,next){
var that=this;
if(that.arr[0].value.length==0||to.name=='Count'){
next()
}else{
this.$vux.confirm.show({
title: '',
content: '',
confirmText:"",
cancelText:"",
onShow () {
},
onHide () {

},
onCancel () {
localStorage.removeItem('saleReport');
next()
},
onConfirm () {
localStorage.setItem('saleReport',JSON.stringify({saleReportArr:that.arr,Aclass:that.Aclass,Bclass:that.Bclass,captain:that.helpCaptain,time:new Date().getTime()}))
next()
}
})
}

},
computed:{

},
}



