
import Tab from 'vux/src/components/tab/tab.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'

import reportService from '../service/reportService'
import saleReportService from '../service/saleRepotService'

const PROJ_LIST = 'ROSE_PROJ_LIST'
const PAGE_SIZE = 30
export default {
components: {
Tab,
Cell,
Group,
TabItem,
Calendar,
CellFormPreview,
Grid,
GridItem,
PopupPicker,
Selector,
Divider
},
data() {
return {
showTab: true, // 
activeIndex: 0,
filterDate: [], // 
reportList: [], // 
curTab: 'days',
dateList: [
{
name: '',
value: 'days'
},
{
name: '',
value: 'weeks'
},
{
name: '',
value: 'months'
},
{
name: '',
value: 'years'
},
], // 
reportData: { // 
days: [],
weeks: [],
months: [],
years: []
},
filterParams: {}, // 
showDate: false, // 
dateSelected: { // 
name: '',
value: 'days'
},
showProj: false, // 
projList: [], // 
page: 1, // 
curPage: 1, // 
objName: '', // 
isDisabled: false
}
},
methods: {
onItemClick(item) {
this.curTab = item.value
this.reportList = this.reportData[item.value]
},
onChange(val) {
console.log(val);

},

resetReportData() {
this.reportData = {
days: [],
weeks: [],
months: [],
years: []
}
},

assembleData(params = {}) {
reportService.getReport(Object.assign(this.filterParams, {
objName: this.objName,
pageNo: this.page
})).then(data => {
this.resetReportData()
let map = ['days', 'weeks', 'months', 'years']

map.forEach(item => {
data[item].forEach((data, index) => {
let detail = [
{
label: '',
value: `${data.qty || 0}/`
},
{
label: 'A',
value: `${numberComma(data.aProduct) || 0}`
},
/*{
label: 'B',
value: '999'
}, */{
label: '',
value: data.bmName || ''
}, {
label: '',
value: data.bankName || ''
}
];
if (this.objName === '') {
detail.shift()
}
this.reportData[item].push({
name: `${(index + 1) + (this.page - 1) * PAGE_SIZE}. ${data.creator}`,
sales: '',
showContent: false,
detail: detail
})
})
})
this.curPage = this.page
this.reportList = this.reportData[this.dateSelected.value]
this.isDisabled = this.reportList.length < PAGE_SIZE
}).catch(err => {
this.resetReportData()
this.reportList = []
})
},

getProj() {
let proj = JSON.parse(sessionStorage.getItem(PROJ_LIST))
proj.shift()
this.projList = proj
},

hideDropList() {
this.showDate = false;
this.showProj = false;
},

dateClick() {
this.showDate = !this.showDate;
this.showProj = false;
},

dateItemClick(item) {
this.showDate = false;
this.dateSelected = item;
if (this.page === 1) {
this.reportList = this.reportData[item.value];
} else {
this.page = 1
this.assembleData()
}
},

projClick() {
this.showProj = !this.showProj;
this.showDate = false;
},

projItemClick(item) {
this.showProj = false;
this.objName = item;
this.page = 1
this.assembleData()
},

aProjClick() {
this.showProj = false;
this.showDate = false;
this.objName = '';
this.page = 1
this.assembleData();
},

pagePrev() {
if (this.page === 1) {
return
}
this.page--;
this.assembleData();
},

pageNext() {
if (this.isDisabled) {
return
}
this.page++;
this.assembleData();
},
},
created() {
let query = this.$route.query
console.log(query.proj)
this.filterParams = {
shengName: query.region || '', // 
bankName: query.bank || '', // 
bmName: query.dept || '', // 
objName: query.proj || '', // 
}

if (this.filterParams.objName) {
this.objName = this.filterParams.objName;
}
this.assembleData(this.filterParams)
this.getProj()
}
}



