
import saleRepotService from '../service/saleRepotService'
import Alert from 'vux/src/components/alert/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import querystring from 'vux/src/tools/querystring/index.js'
import numberComma from 'vux/src/tools/number/comma.js'

export default {
components:{
Cell,
Group,
XInput,
Confirm,
XButton,
Selector,
PopupPicker,
Alert
},
data () {
return {
alertEnd:false,
list: [],
arr:[
{
value:[],
qty:''
}
],
Aclass:'',
Bclass:'',
showNumber:false,
showNewDiv:false,
mescroll: null,
totalInfo:''
}
},
filters:{
numberComma
},
methods:{

getPickerValue(val){

},
onChange(val){
},
createNew(){
this.arr.push({
value: [],
qty:''
}); //
},
deleteNew(){
if(this.arr.length==1){
this.arr=[
{
value: [],
qty:''
}
]
}else{
this.arr.splice(this.arr.length-1,1); 
}
},

guid() {
function S4() {
return (((1+Math.random())*0x10000)|0).toString(16).substring(1);
}
return (S4()+S4()+"-"+S4()+"-"+S4()+"-"+S4()+"-"+S4()+S4()+S4());
},
listData(){
saleRepotService.saleRepotList().then(data=>{
for(let i = 0 ; i<data.tableContent.length ; i++){
this.list.push({
name:data.tableContent[i]['trans_detail_uncalc.transObjCode'],
value:data.tableContent[i]['trans_detail_uncalc.transObjCode']+'_'+i,
parent:'0',
},{
name:data.tableContent[i]['trans_detail_uncalc.price'],
value:data.tableContent[i]['trans_detail_uncalc.price'],
parent:data.tableContent[i]['trans_detail_uncalc.transObjCode']+'_'+i,
});
}
})
},
end(){
var that=this;
var jsonData = {
"listId": "4bda3e47-a088-4749-a988-ebb07cfb00e4",
"referenceId":this.guid(),
"baseinfoExt": {
"id":this.guid(),
"varchar1": JSON.parse(localStorage.getItem('ROSE_OPTION')).dept,
"varchar2": JSON.parse(localStorage.getItem('ROSE_OPTION')).captain,
"varchar3": JSON.parse(localStorage.getItem('ROSE_OPTION')).region,
"varchar4": JSON.parse(localStorage.getItem('ROSE_OPTION')).bank,
},
"transDetailUncalc": [],
"transCode": "XHXSDD"
};
if(this.arr[0].value.length==0){
this.$vux.alert.show({
title: '',
content: '',
onShow () {

},
onHide () {

}
})
return;
}
for(let i =0 ;i<this.arr.length ; i++){
if(this.arr[i].qty==''){
this.$vux.alert.show({
title: '',
content: '',
onShow () {

},
onHide () {

}
})
return;
}else{
jsonData.transDetailUncalc.push({
"id":this.guid(),
"transObjCode": this.arr[i].value[0].split('_')[0],//
"containerCode": "",//
"qty": this.arr[i].qty,
"amount": this.arr[i].qty*this.arr[i].value[1],//
"fgCode": ""
})
}
}
if(this.Aclass==''){
this.$vux.alert.show({
title: '',
content: 'A',
onShow () {

},
onHide () {

}
})
return;
}else if(this.Bclass==''){
this.$vux.alert.show({
title: '',
content: 'B',
onShow () {

},
onHide () {

}
})
return;
}else{
jsonData.transDetailUncalc.push({
"id":this.guid(),
"transObjCode": "A",//
"containerCode": "A",//
"qty": '',
"amount": this.Aclass,//
"fgCode": ""
},{
"id":this.guid(),
"transObjCode": "B",//
"containerCode": "B",//
"qty": '',
"amount": this.Bclass,//
"fgCode": ""
})
}
let totalInfo={
'isMobile':true,
'conn':20000,
'list':'trans_form_data',
'transCode':'XHXSDD',
'jsonData':JSON.stringify(jsonData)
}
this.totalInfo=totalInfo;
localStorage.setItem('saleReportInfo',JSON.stringify({saleReportRemark:totalInfo,time:new Date().getTime()}))
localStorage.setItem('saleReport',JSON.stringify({saleReportArr:this.arr,Aclass:this.Aclass,Bclass:this.Bclass,time:new Date().getTime()}))
that.$router.push({path:'/count'})

},
Ainput(e){
this.Aclass=e.split(',').join('');
},
Binput(e){
this.Bclass=e.split(',').join('');
},
timestampToTime(timestamp) {
var date = new Date(timestamp);
var Y = date.getFullYear() + '-';
var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
var D = date.getDate() + ' ';
return Y+M+D;
},
DateDifference(Date1,Date2) {
var sDate, newDate1, newDate2, Days;
var aDate = Date1.split("-");
var newDate1 = new Date(aDate[1] + '-' + aDate[2] + '-' + aDate[0]); 
var  aDate = Date2.split("-");
var newDate2 = new Date(aDate[1] + '-' + aDate[2] + '-' + aDate[0]);
var  Days = parseInt(Math.abs(newDate1 - newDate2) / 1000 / 60 / 60 / 24); 
return Days;
},
letMeTest(){
let path = this.$router.path;
}
},
created(){

},
mounted(){
if(localStorage.getItem('saleReport')){
this.arr=JSON.parse(localStorage.getItem('saleReport')).saleReportArr;
this.Aclass=JSON.parse(localStorage.getItem('saleReport')).Aclass;
this.Bclass=JSON.parse(localStorage.getItem('saleReport')).Bclass;
}
this.listData();
},
beforeRouteLeave(to,from,next){
var that=this;
if(that.arr[0].value.length==0||to.name=='Count'){
next()
}else{
this.$vux.confirm.show({
title: '',
content: '',
confirmText:"",
cancelText:"",
onShow () {
},
onHide () {

},
onCancel () {
localStorage.removeItem('saleReport');
next()
},
onConfirm () {
localStorage.setItem('saleReport',JSON.stringify({saleReportArr:that.arr,Aclass:that.Aclass,Bclass:that.Bclass,time:new Date().getTime()}))
next()
}
})
}

},
computed:{

},
}



