
import optionService from '../service/optionService'
import saleReportService from '../service/saleRepotService'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'


const FILTER_OPTION = 'ROSE_FILER_OPTION'; 
const PROJ_LIST = 'ROSE_PROJ_LIST'; 
export default {
components: {
Group,
XButton,
PopupPicker
},
data() {
return {
region: [],
whichIndex: '',
showMore: false,
pickerList: [],
regionList: [],
bankList: [],
deptList: [],
projList: []
}
},
computed: {
regionTitle() {
return this.region[0] ? this.region[0].split('-')[1] + '' : ''
}
},
methods: {

getRegion(params = {}) {
return new Promise((resolve, reject) => {
if (this.regionList.length > 0) {
resolve(this.regionList)
} else {
optionService.getRegion().then(data => {
let region = data.reduce((arr, item) => {
arr.push(item.name);
return arr
}, ['']);
this.regionList = region
resolve(region)
})
}
})
},

getBank(params = {}) {
return new Promise((resolve, reject) => {
if (this.bankList.length > 0) {
resolve(this.bankList)
} else {
optionService.getBank().then(data => {
let bank = data.tableContent.reduce((arr, item) => {
arr.push(item.bankName);
return arr
}, ['']);
this.bankList = bank
resolve(bank)
})
}
})
},

getDept(params = {}) {
return new Promise((resolve, reject) => {
if (this.deptList.length > 0) {
resolve(this.deptList)
} else {
optionService.getDept().then(data => {
let dept = data.tableContent.reduce((arr, item) => {
arr.push(item.groupName);
return arr
}, ['']);
this.deptList = dept
resolve(dept)
})
}
})
},

getProj() {
return new Promise((resolve, reject) => {
if (this.projList.length > 0) {
resolve(this.projList)
} else {
saleReportService.saleRepotList().then(data => {
let proj = data.tableContent.reduce((arr, item) => {
arr.push(item['trans_detail_uncalc.transObjCode']);
return arr
}, ['']);
this.projList = proj
sessionStorage.setItem(PROJ_LIST, JSON.stringify(proj))
resolve(proj)
})
}
})
},
getPickerValue(index, value) {
if (!value) {
this.$set(this.pickerList[index], 'value', [])
}
this.showMore = true;
this.whichIndex = index;
if (index === 0) {
this.region = value;
} else if (index === 2) {
this.showMore = false;
}
},
createNew() {

let index = this.whichIndex;
if (index === 0) {
this.getBank().then(bank => {
this.pickerList.push({
title: '',
ph: '',
value: [],
list: [bank]
})
})
this.showMore = false;
} else if (index === 1) {
this.getDept().then(dept => {
this.pickerList.push({
title: '',
ph: '',
value: [],
list: [dept]
})
})
this.showMore = false;
}
},

goRp() {
let filterParams = {}
this.pickerList.forEach((item, index) => {
switch (index) {
case 0:
filterParams.region = item.value[0]
break;
case 1:
filterParams.dept = item.value[0]
break;
case 2:
filterParams.bank = item.value[0]
break;
case 3:
filterParams.proj = item.value[0]
break;
}
})
if (!filterParams.region && !filterParams.dept && !filterParams.bank && !filterParams.proj) {
this.$vux.alert.show({
content: ''
});
return
}
console.log(filterParams)
sessionStorage.setItem(FILTER_OPTION, JSON.stringify(this.pickerList))
this.$router.push({
path: '/reportsOp/reports',
query: filterParams
})
},
},
created() {
(async () => {
this.pickerList = [
{
title: '',
ph: '',
value: [],
list: []
},
{
title: '',
ph: '',
value: [],
list: []
},
{
title: '',
ph: '',
value: [],
list: []
},
{
title: '',
ph: '',
value: [],
list: []
}
]
let filter = JSON.parse(sessionStorage.getItem(FILTER_OPTION))
if (!filter) {
let tmpPickerList = []
await this.getRegion().then(region => {
tmpPickerList.push({
title: '',
ph: '',
value: [],
list: [region]
})
})
await this.getBank().then(bank => {
tmpPickerList.push({
title: '',
ph: '',
value: [],
list: [bank]
})
})
await this.getDept().then(dept => {
tmpPickerList.push({
title: '',
ph: '',
value: [],
list: [dept]
})
})
await this.getProj().then(proj => {
tmpPickerList.push({
title: '',
ph: '',
value: [],
list: [proj]
})
})
this.pickerList = tmpPickerList
} else {
await this.getRegion()
await this.getDept()
await this.getBank()
await this.getProj()
this.pickerList = filter
}
})()
},
destroyed() {
sessionStorage.removeItem(FILTER_OPTION)
}
}



