
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'

import myReportService from '../service/myReportService'
const list = () => ['', '', '', '']
export default {
components:{
Tab,
TabItem,
Divider,
SwiperItem,
FormPreview,
CellFormPreview
},
data(){
return{
listTab: list(),
demo2: '',
list1:[],
list2:[],
list3:[],
list1Total:0,
list2Total:0,
list3Total:0,
list4:[
{ label: '999',value: '20000 /' },
{ label: '888',value: '12312 /' },
{ label: '666',value: '4232 /' }
],
remark:'',
}
},
methods:{
mylist(tab){
myReportService.myRepotList().then(data=>{
this.remark=data;
if(tab==0){
this.listpanl(this.remark.days)
}else if(tab==1){
this.listpanl(this.remark.weeks)
}else if(tab==2){
this.listpanl(this.remark.months)
}else if(tab==3){
this.listpanl(this.remark.years)
}
});

},
listpanl(m){
if(m==undefined||m.length==0){
this.list1Total=0;
this.list2Total=0;
this.list3Total=0;
this.list1.length=0;
}else{
this.list1Total=m[0].amount;
this.list2Total=m[0].aProduct;
this.list3Total=m[0].bProduct;
this.list1[0]={
label:'',
value:m[0].qty,
}
}
}
},
created(){
},
mounted(){
this.mylist(0);
}
}



