import axios from 'axios';
import $axios from '../plugins/ajax'
import conf from "../plugins/ajax/conf";
import querystring from 'vux/src/tools/querystring/index.js'


const TOKEN_KEY = 'ROSE_LOGIN_TOKEN';

let tokenService = {
/**
* toke
*/
clean() {
window.localStorage.removeItem(TOKEN_KEY);
},
/**
* tokenIDtoken
*/
getToken(key = 'key') {
let token = this.checkLogin(key)
if (token) {
return new Promise((resolve, reject) => {
resolve(token)
})
} else {
return this.login()
}
},
/**
* token
*/
setToken(data) {
window.localStorage.setItem(TOKEN_KEY, JSON.stringify({
entityId: data.entityId,
key: data.token,
timestamp: +new Date()
}));
},

checkLogin(key = 'key') {
let token = JSON.parse(window.localStorage.getItem(TOKEN_KEY)) || {}
if (token[key]) {
let timestamp = token.timestamp
if (new Date() - timestamp > (12 * 3600 * 1000)) { // 12
return ''
}
} else {
return ''
}
return token[key]
},

login() {
let isQYWX = navigator.userAgent.toLowerCase().match(/wxwork/) !== null; 
if (isQYWX) {
return this.pcLogin()
} else {
return this.pcLogin()
}
},

pcLogin() {
return new Promise((resolve, reject) => {
let params = {
method: 'post',
baseURL: '/H_roleplay-si',
url: '/login',
headers: {
'Content-Type': 'application/json',
},
data: {
loginModel: 1,
password: 'rfd120',
userCode: 'rfd120'
}
}

axios(params).then((res) => {
let data = res.data
this.clean()
this.setToken({
token: data.token,
entityId: data.entityId
})
resolve(data.token)
}).catch(function (error) {
let data = error.response && error.response.data
reject(error.response.data)
});
}
)
},

QYWXLogin() {
return new Promise((resolve, reject) => {
let query = querystring.parse(location.search.slice(1))
let code = query.code || ''
axios.get('/H_roleplay-si/wxLogin?code=' + code + '&&state=1').then((res) => {
let data = res.data;
this.clean();
this.setToken({
token: data.token,
entityId: data.entityId
});
})
})
},
/**
* token 
*/
init: function (qwUserCode) {
axios.get('/H_roleplay-si/wxLogin?code=' + qwUserCode + '&&state=1').then((response) => {
const result = response.data;
this.clean();
this.setToken({
token: result.token,
entityId: result.entityId
});
})







}
}
export default tokenService;


