import $axios from '../plugins/ajax'

let optionService = {
  // TODO 
  getRegion() {
    return $axios.ajax({
      url: '/base/dict/getListByValue',
      data: {
        value: 'ACCOUNTPROVINCE'
      }
    });
  },
  // TODO 
  getBank() {
    return $axios.ajax({
      url: '/ds/getBankForWarehouse',
    });
  },
  // TODO 
  getDept() {
    return $axios.ajax({
      url: '/ds/getAPartGroup',
    });
  },
  // TODO 
  getCaptain(data = {}) {
    return $axios.ajax({
      url: '/ds/getRoleUser2Oneself',
      data: {
        roleId: '86',
        filter: JSON.stringify([{
          operator: 'like',
          value: data.value || '',
          property: 'nickname'
        }])
      }
    })
  }
}

export default optionService


